import { PricingCardProps } from "@/models/pricingCardProps";
import Button from "./Button";

import { IoMdCheckmarkCircleOutline } from "react-icons/io";

const PricingCard = ({ title, price, subscriptionInfo }: PricingCardProps) => {
  return (
    <div className="backdrop-blur-2x hover:ring-indigo-500l h-100 w-full rounded-4xl p-8 ring-1 ring-white/60 transition-all duration-300 ease-in-out hover:ring-indigo-500">
      <h1 className="mb-4 cursor-default text-2xl font-medium text-[oklch(0.90_0_0)]">
        {title === "premiumSubscription" ? "Premium" : "7-Day Free Trial"}
      </h1>
      <div className="mb-16">
        <span className="mr-2 cursor-default text-5xl font-bold text-[oklch(0.95_0_0)]">{`$${price}`}</span>
        <span className="cursor-default text-[oklch(0.75_0_0)]">/mo</span>
      </div>
      <div className="flex flex-col">
        {subscriptionInfo.map((info, index) => (
          <div className="flex items-center">
            <span className="mr-1 text-[oklch(0.75_0_0)]">
              <IoMdCheckmarkCircleOutline />
            </span>
            <span className="text-[oklch(0.75_0_0)]" key={index}>
              {info}
            </span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default PricingCard;
